import React, { PureComponent } from 'react';
import { ThemeProvider } from 'styled-components';

import { Section } from '../components';
import * as COLORS from '../patterns/colors';

export class WelcomeSection extends PureComponent {

    get theme() {
        const { theme: descendantTheme } = this.props;
        const { NAMESPACE: SECTION } = Section.NAMESPACE;
        const { BACKGROUND_COLOR: SECTION_BACKGROUND_COLOR } = Section.THEME_PROPERTIES;

        return inheritedTheme => ({
            [SECTION]: {
                [SECTION_BACKGROUND_COLOR]: COLORS.DARK_GREY
            },
            ...descendantTheme
        })
    }

    render() {
        const { theme } = this;

        return (<ThemeProvider theme={theme}>
            <Section>
                <Logo />
            </Section>
        </ThemeProvider>);
    }
}