import React, { PureComponent } from "react";
import { createShadowComponent } from './helpers';
import * as COLORS from '../../patterns/colors';

const Wrapper = styled.div`
    height: 256px;
    width: 256px;
`;

const CyanShadow = createShadowComponent(COLORS.ABSOLUTE_CYAN, 15, 5);

const MagentaShadow = createShadowComponent(COLORS.ABSOLUTE_MAGENTA, 12, 0);

const YellowShadow = createShadowComponent(COLORS.ABSOLUTE_YELLOW, 20, 10);

const LogoImage = styled(createShadowComponent(COLORS.BLACK, 12, 6))`
    background-image: url("");
`;

export default class Logo extends PureComponent {
    render() {
        return (<Wrapper>
            <CyanShadow />
            <MagentaShadow />
            <YellowShadow />
            <LogoImage />
        </Wrapper>);
    }
}